// localStorage
/*
*  let Str  =localStorage.getItem(key);
*   localStorage.setItem(key,value) ,value 
*    localStorage.removeItem(key);
* */

// 1.0 key,localStoragekey
export const KEY = 'goodsdata';
export let valueObj = {goodsid:0,count:0};

// 2.0 
// value; {goodsid:87,count:10}
export function setItem(value){
    //1.0 json
    let jsonString =  localStorage.getItem(KEY);
    jsonString = jsonString || '[]';
    let arr =   JSON.parse(jsonString);
    // 2.0 valuearr
    arr.push(value);
    // 3.0 arr json
    localStorage.setItem(KEY,JSON.stringify(arr));
}

// 3.0 
export function getItem(){
   let jsonString =  localStorage.getItem(KEY);
//    json js
    // jsonStringjson
    jsonString = jsonString || '[]';

    return JSON.parse(jsonString);
}

// 4.0  goodsid:id
export function remoteItem(goodsid){
    let arr = getItem();
//    arrgoodsidgoodsid
    for(let i= arr.length -1;i>=0 ; i--){
        if(arr[i].goodsid == goodsid){
            arr.splice(i,1);
        }
    }

//    arrlocalStorage
    localStorage.setItem(KEY,JSON.stringify(arr));

}

// 5.0 localStorage
/*
* 
* {
*  id,
*  id
* }
* 
* {
*   87:4,
*   88:1
* }
* */
export function getgoodsObject(){
//    
/*
* arr
* [{goodsid:87,count:1},{goodsid:87,count:3},{goodsid:88,count:1}]
* */
    let arr  = getItem();
// arrgoodid
    let resObj ={};
    for(let i = 0 ; i<arr.length; i++){
        let item = arr[i];
        if(!resObj[item.goodsid]){
        //    
            resObj[item.goodsid] = item.count;
        }else{
        //    cont
            let count = resObj[item.goodsid];
            resObj[item.goodsid] = count + item.count;
        }
    }

    return resObj;
}

//obj{goodsid:87,type:'add'}
export function updateData(obj){
    let arr  = getItem();

    let count = 1;
    if(obj.type =='add'){
        //
        arr.push({goodsid:obj.goodsid,count:count});
    }else{
        //
        for(let i =0 ;i < arr.length ; i++){
            //count1count1
            if(arr[i].goodsid == obj.goodsid){
                if(arr[i].count > 1){
                    arr[i].count = arr[i].count -1;
                    break;
                }else{
                    //
                    arr.splice(i,1);
                    break;
                }
            }
        }
    }

//    arrlocalStorage
    localStorage.setItem(KEY,JSON.stringify(arr));

}




