<template>
	<div id="tmpl">
		<h4 v-text="info.title"></h4>
		<p class="line"></p>
		<p v-html="info.content"></p>
	</div>
</template>

<script>
	import common from '../../kits/common.js';
	import { Toast } from 'mint-ui';

	export default{
		data(){
			return {
				id:0, // id
				info:{
//					"title": "HUAWEI6Plus 16G4G",
//					"content": "<p>\r\n\t6 PlusPE-TL10PE-UL00PE-TL20PE-TL00M7.5mmTDD-LTE/TD-SCDMA/GSM800+8005.51080p1.8GHz9253GB RAM+32GB ROM\r\n</p>\r\n<p align=\"center\">\r\n\t<span style=\"color:#FF0000;font-size:16px;\"><img class=\"gomeImgLoad\" alt=\"\" src=\"http://img5.gomein.net.cn/image/bbcimg/productDesc/descImg/201503/desc04/A0004794664/3997361.jpg\" /></span> \r\n</p>"
					}
			}
		},
		created(){
			this.id = this.$route.params.id;

			this.getinfo();
		},
		methods:{
			getinfo(){
				let url =common.apidomain +'/api/goods/getdesc/'+this.id;
				this.$http.get(url).then(function(res){
					if(res.body.status != 0){
						Toast(res.body.message);
						return;
					}
					this.info = res.body.message[0];
				});
			}
		}
	}

</script>

<style scoped>
	#tmpl{
		padding: 5px;
	}
#tmpl h4{
	color: #0094ff;
}

	#tmpl .line{
		height: 1px;
		border:1px solid rgba(0,0,0,0.3);
	}
</style>


