<template>
	<div id="tmpl">
	  <!--1.0 -->
		<div class="silder">
		<silder :imgs="imgs"></silder>
		</div>

		<!--2.0 -->
		<div id="buy">
			<h4 v-text="info.title"></h4>
			<p class="line"></p>
			<ul>
				<li class="price">
					: <s>{{info.market_price}}</s>    <span>{{info.sell_price}}</span>
				</li>
				<li class="inputli">
					 <inputnumber v-on:dataobj="getcount" class="inputnumber"></inputnumber>
					<transition name="show"
					 @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter"
					>
						<div v-if="isshow" class="ball"></div>
					</transition>
				</li>
				<li>
					<mt-button type="primary" size="small"></mt-button>
					<mt-button type="danger" size="small" @click="toshopcar"></mt-button>
				</li>
			</ul>
		</div>

		<div id="params">
				<h6></h6>
				<p class="line"></p>
			<ul>
				<li>{{info.goods_no}}</li>
				<li>{{info.stock_quantity}}</li>
				<li>{{info.add_time | datefmt('YYYY-MM-DD')}}</li>
			</ul>
		</div>

		<!--3.0 -->
		<!--4.0 -->
		<div id="other">
			<router-link v-bind="{to:'/goods/goodsdesc/'+id}">
				<mt-button class="imgdesc" type="primary" size="large"></mt-button>
			</router-link>
			<router-link v-bind="{to:'/goods/goodscomment/'+id}">
				<mt-button type="danger" size="large"></mt-button>
			</router-link>
		</div>
	</div>
</template>

<script>
	import silder from '../subcom/silder.vue';
	import common from '../../kits/common.js';
	import inputnumber from '../subcom/inputNumber.vue';
//	vm
	import {vm,COUNTSTR} from '../../kits/vm.js';
	import {setItem,valueObj} from '../../kits/localSg.js'

	export default{
		components:{silder,inputnumber},
		data(){
			return {
				isshow :false , //
				inputNumberCount:1, //
				id : 0,  //id
				imgs:[],
				info:{
//					"id": 87,
//					"title": "HUAWEI6Plus 16G4G",
//					"add_time": "2015-04-19T16:51:03.000Z",
//					"goods_no": "SD9102356032",
//					"stock_quantity": 60,
//					"market_price": 2499,
//					"sell_price": 2195
				}  // 
			}
		},
		created(){
//			urlid
			this.id = this.$route.params.id;
			this.getimgs();
			this.getinfo();
		},
		methods:{
//			3
			beforeEnter(el){
//				
				el.style.transform = "translate(0px,0px)";
			},
			enter(el,done){
//				
				el.offsetWidth;

//				
				el.style.transform = "translate(75px,366px)";

//				
				done();
			},
			afterEnter(el){
//			
				this.isshow = !this.isshow;
			},
//			
			toshopcar(){
//				1.0 
				vm.$emit(COUNTSTR,this.inputNumberCount);
//				2.0 localStroage
				valueObj.goodsid = this.id;
				valueObj.count = this.inputNumberCount;
				setItem(valueObj);

//				3.0 
				this.isshow = !this.isshow;
			},
//			inputnumber
			getcount(count){
				this.inputNumberCount = count;
			},
//			1.0 
			getinfo(){
				let url = common.apidomain +'/api/goods/getinfo/'+this.id;
				this.$http.get(url).then(function(res){
					//
					this.info =res.body.message[0];
				});
			},
			getimgs(){
				let url  = common.apidomain + '/api/getthumimages/'+this.id;
				this.$http.get(url).then(function(res){
					//
				this.imgs =res.body.message;
				});
			}
		}
	}

</script>

<style scoped>
 .silder{
	 border:1px solid rgba(0,0,0,0.4);
	 border-radius: 5px;
	 margin: 5px;
 }

	#buy,#params,#other
	{
		margin: 5px;
		padding: 5px;
		border:1px solid rgba(0,0,0,0.4);
		border-radius: 5px;
	}
 .line{
		height: 1px;
		border: 1px solid rgba(0,0,0,0.2);
	}
	#buy ul,#params ul{
		padding-left: 0px;
	}
 #buy h4{
	 color:#0094ff;
	padding: 5px;
 }
 #buy li,#params li{
	 list-style: none;
	 padding: 8px;
 }

	#buy .price span{
		color:red;
	}

	#other .imgdesc{
		margin-bottom: 20px;
	}
	.inputli{
		position: relative;
	}
	.inputnumber{
		position: absolute;
		left:100px;
		top:5px;
	}

	.ball{
		background-color: red;
		height: 20px;
		width: 20px;
		border-radius: 50%;
		position: absolute;
		left:150px;
		top:10px;
		transition: all 0.4s ease;
		z-index: 100;
	}
</style>


