<template>
	<div id="tmpl">
        <!--1.0 -->
	   <div class="title">
		   <h4 v-text="info.title"></h4>
		   <p>{{ info.add_time | datefmt('YYYY-MM-DD') }}  {{info.click}}</p>
	   </div>
		<div id="content" v-html="info.content" ></div>

        <!--2.0 -->
        <!--10.2 -->
        <comment :id = "id"></comment>
	</div>
</template>

<script>
    import common from '../../kits/common.js';
//    10.0  comment.vue
    import comment from '../subcom/comment.vue';

	export default{
        components:{
            comment  // 10.1 
        },
        data(){
            return {
                id: 0,  // id
                info: {
//                    "id": 13,
//                    "title": "150% ",
//                    "click": 1,
//                    "add_time": "2015-04-16T03:50:28.000Z",
//                    "content":"sdfsdf"}
                }
            }
        },
        created()
            {
//			1.0 urliddataid
                this.id = this.$route.params.id;

//            2.0 id
            this.getinfo();
            },
        methods:{
            getinfo(){
//                1.0 url
                let url = common.apidomain + '/api/getnew/'+this.id;

//                2.0 ajax
                this.$http.get(url).then(function(res){
                    let body = res.body;
                    if(body.status != 0){
                        alert(body.message);
                        return;
                    }

//                    3.0 
                    this.info = body.message[0];
                });
            }
        }
    }

</script>

<style scoped>
	.title h4{
		color: #0094ff;
	}
	.title p{
		color:rgba(0,0,0,0.5);
	}

	.title,#content{
		padding: 5px;
	}

</style>


