// 1.0 vue
import Vue from 'vue';

// 2.0 App.vuevue
import App from './App.vue';

// 3.0 vue-router
import vueRouter from 'vue-router';
// 3.0.1 vueRouterVue
Vue.use(vueRouter);

// 3.0.2 
import home from './components/Home.vue';
import shopcar from './components/shopcar/car.vue';
import newslist from './components/news/newslist.vue';
import newsinfo from './components/news/newsinfo.vue';
import photolist from './components/photo/photolist.vue';
import photoinfo from './components/photo/photoinfo.vue';
import goodslist from './components/goods/goodslist.vue';
import goodsinfo from './components/goods/goodsinfo.vue';
import goodsdesc from './components/goods/goodsdesc.vue';
import goodscomment from './components/goods/goodscomment.vue';
import car from './components/shopcar/car.vue';


// 3.0.2 
let router1 = new vueRouter({
	linkActiveClass:'mui-active',  //class
	routes:[
		{path:'/',redirect:'/home'},//  /home
		{path:'/home',component:home},   //
		{path:'/shopcar',component:shopcar},  // 
		{path:'/news/newslist',component:newslist}, //
		{path:'/news/newsinfo/:id',component:newsinfo},  // 
		{path:'/photo/photolist',component:photolist},
		{path:'/photo/photoinfo/:id',component:photoinfo},
		{path:'/goods/goodslist',component:goodslist},
		{path:'/goods/goodsinfo/:id',component:goodsinfo},
		{path:'/goods/goodsdesc/:id',component:goodsdesc},
		{path:'/goods/goodscomment/:id',component:goodscomment},
		{path:'/shopcar/car',component:car}
	]
	});


// 4.0 mint-ui
// mint-uicss
import 'mint-ui/lib/style.min.css';
// mint-ui
import mintui from 'mint-ui';
// Vuemintui
Vue.use(mintui);

// 5.0 muicss
import '../statics/mui/css/mui.css';

// 6.0 
import '../statics/css/site.css';

// 7.0 vue-resourcevuevue$httpajax
import vueResource from 'vue-resource';
Vue.use(vueResource);

// 8.0 
import moment from 'moment';
Vue.filter('datefmt',function(input,fmtstring){
//	momentjs
	return moment(input).format(fmtstring);
});

// 9.0 
import VuePreview from 'vue-preview';
Vue.use(VuePreview);

// 5.0 Vue
new Vue({
	el:'#app',
	// 
	router:router1,
	// render:function(create){create(App)} //es5
	render:c=>c(App)  // es6 =>goes to
});


