<!--  -->
<template>
	<div>
		<!-- 1.0 mint-uiheader -->
		<mt-header fixed title=".Vue"></mt-header>
			<!--1.0.1 -->
		<div v-if="isshow" id="back">
			<a href="javascript:void(0)" @click="backto"></a>
		</div>
		<!-- 2.0 vue-router <router-view> -->
		<router-view></router-view>

		<!-- 3.0 muitabbar -->
		<nav class="mui-bar mui-bar-tab">
			<router-link class="mui-tab-item " to="/home">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label"></span>
			</router-link>
			<router-link class="mui-tab-item" to="/tabbar-with-chat">
				<span class="mui-icon mui-icon-email">

				</span>
				<span class="mui-tab-label"></span>
			</router-link>
			<router-link class="mui-tab-item" to="/shopcar/car">
				<span class="mui-icon mui-icon-contact">
					<span id="badge" class="mui-badge">0</span>
				</span>
				<span class="mui-tab-label"></span>
			</router-link>
			<router-link class="mui-tab-item" to="/tabbar-with-map">
				<span class="mui-icon mui-icon-gear"></span>
				<span class="mui-tab-label"></span>
			</router-link>
		</nav>
	</div>
</template>

<script>

	import {vm,COUNTSTR} from './kits/vm.js';
	//  vm.$on()  COUNT
	vm.$on(COUNTSTR,function(count){
//		1.0 count
		let badgeobj = document.querySelector('#badge');
		badgeobj.innerText = parseInt(badgeobj.innerText) + count;
	});

	export default{  // es6
		data(){  // es5 data:function(){
			return {
				isshow : false
			}
		},
		watch:{
			'$route':function(newroute,oldroute){
				if(newroute.path.toLowerCase() == '/home'){
					this.isshow = false;
				}else{
					this.isshow = true;
				}
			}
		},
		methods:{
			backto(){
//				go(-1)(vue-router)
				this.$router.go(-1);
			}
		},
	/*	created(){

		}*/
	}
</script>

<style scoped>
/*cssscopedcss*/
#back{
	width: 60px;
	position: absolute;
	top:10px;
	left:10px;
	z-index: 101;
}
#back a{
	color:white;
	font-size: 16px;
	font-weight: bold;
}
</style>



