<template>
	<div id="tmpl">
		<!--1.0 -->
		<ul class="mui-table-view">
			<li v-for="item in list" class="mui-table-view-cell mui-media">
				<router-link v-bind="{to:'/news/newsinfo/'+item.id}" >
					<img class="mui-media-object mui-pull-left" :src="item.img_url">
					<div class="mui-media-body">
						{{item.title}}
						<p class='mui-ellipsis' v-text="item.zhaiyao"></p>
						<div class="ft">
							<span>:{{item.add_time | datefmt('YYYY-MM-DD HH:mm:ss')}}</span>
							<span class="click">{{item.click}}</span>
						</div>
					</div>
				</router-link>
			</li>

		</ul>

	</div>
</template>

<script>
import { Toast } from 'mint-ui';
import common from '../../kits/common.js';

	export default{
		data(){
			return {
				list:[] //
			}
		},
		created(){
			this.getnewslist();
		},
		methods:{
		// api
		getnewslist(){
			// 1.0 url
			let url = common.apidomain+'/api/getnewslist';

			// 2.0 $http.get
			this.$http.get(url).then(function(res){
				// 3.0 
					let body = res.body;
				/*  
				{
					  "status": 0,   // 0 :  0
					  "message": [
						{
						  "id": 13,
						  "title": "150% ",
						  "add_time": "2015-04-16T03:50:28.000Z",
						  "zhaiyao": "",
						  "click": 1,
						  "img_url": "http://www.webhm.top:8080/upload/201504/16/201504161149414479.jpg"
						},
						{
						  "id": 14,
						  "title": "20155",
						  "add_time": "2015-04-16T04:05:34.000Z",
						  "zhaiyao": "20154",
						  "click": 2,
						  "img_url": "http://www.webhm.top:8080/upload/201504/16/201504161205596364.jpg"
						}
						]

				*/
				// 4.0 0
				if(body.status != 0){
					Toast(body.message);
					return;
				}

				// 5.0 messagethis.list
				this.list = body.message;

			});
		}
		}
	}

</script>

<style scoped>
 .mui-table-view img{
 	height:80px;
 	width:80px;
 }

 .mui-table-view .mui-media-object{
	 max-width: 80px;
	 line-height: 80px;
 }

	.ft{
		margin-top: 1.5em;
		font-size: 14px;
		color:#0094ff;
	}

 .ft .click{
	 margin-left: 20px;
 }
</style>


