<template>
	<div id="tmpl">
		<div class="row" v-for="(item,index) in datalist">
			<mt-switch class="switch" v-model="value[index]"></mt-switch>
			<img class="img" :src="item.thumb_path" alt="" >
			<div class="inforight">
				<h4 v-text="item.title"></h4>
				<div class="bottom">
					<ul>
						<li>{{item.sell_price}}</li>
						<li><carinputnumber :initCount="item.cou" :goodsid="item.id" v-on:cardataobj="getiInputNumber"></carinputnumber></li>
						<li><a href="javascript:void(0)" @click="delrow(item.id,index)"></a></li>
					</ul>

				</div>
			</div>
		</div>

		<div id="total">
			<div class="desc">
				<ul>
					<li></li>
					<li>{{totalcount}},:{{totalAmount}}</li>
				</ul>
			</div>
			<div id="button">
				<mt-button  type="danger" size="small"></mt-button>
			</div>
		</div>
	</div>
</template>

<script>
	import {getgoodsObject,updateData,remoteItem} from '../../kits/localSg.js';
	import common from '../../kits/common.js';
	import { Toast } from 'mint-ui';
	import carinputnumber from '../subcom/carinputNumber.vue';

	export default{
		components:{
			carinputnumber
		},
		computed:{
//			1.0 
			totalcount(){
//				this.valueture
				let trueArr =  this.value.filter(function(item){return item;});
//				
				 //  *  this.datalist
				let amount = 0;
				this.value.forEach((item,index) =>{
//					itemtrue
					if(item){
						let count = this.datalist[index].cou;
						let price = this.datalist[index].sell_price;
						let itemamount =   count * price;
						amount += itemamount;
					}
				});

				this.totalAmount = amount;
				return trueArr.length;
			}
		},
		data(){
			return {
				totalAmount:0, //
				value:[],
				datalist:[
//					{
//						"cou": 1,
//						"id": 87,
//						"title": "HUAWEI6Plus 16G4G",
//						"sell_price": 2195,
//						"thumb_path": "http://www.webhm.top:8080/upload/201504/20/thumb_201504200046594439.jpg"
//					},
//					{
//						"cou": 1,
//						"id": 88,
//						"title": "Apple iPhone 6 Plus 16G 4G",
//						"sell_price": 5780,
//						"thumb_path": "http://www.webhm.top:8080/upload/201504/20/thumb_201504200059017695.jpg"
//					}
				]
			}
		},
		created(){
//			
			this.getdatalist();

		},
		methods:{
			//
			delrow(goodsid,index){
				console.log(goodsid,index);
//			1.0 this.valueindex(totalcount)
				this.value.splice(index,1);
//			2.0 this.datalistindex(totalcount)
				this.datalist.splice(index,1);
//			3.0 localStroagegoodsid
				remoteItem(goodsid);

			},
//			
			getiInputNumber(resObj){
				console.log(resObj);
//			 1.0 localStorage
				updateData(resObj);

//			2.0  this.datalist
				for(let i = 0;i <this.datalist.length ; i++){
					if(this.datalist[i].id == resObj.goodsid){
						if(resObj.type =='add'){
							this.datalist[i].cou = this.datalist[i].cou + 1;
						}else{
							this.datalist[i].cou = this.datalist[i].cou - 1;
						}
						break;
					}
				}

			},
			getdatalist(){
//				1.0 localstorageid
//				obj{
//					87:4,
//					 88:1
//					 }
				let obj = getgoodsObject();
//				2.0 id apiapi
				let idstring = '';
				for(let key in obj){
					idstring+= key + ',';
				}

				idstring = idstring.substring(0,idstring.length-1);

//				3.0 ajaxapithis.datalist
				let url  = common.apidomain + '/api/goods/getshopcarlist/'+idstring;
				this.$http.get(url).then(function(res){
					//
					if(res.body.status != 0 ){
						Toast(res.body.message);
						return;
					}
//					locaStoragecountmessagecou
					res.body.message.forEach((item)=>{
						item.cou = obj[item.id];

					//	bugvaluesfalse
						this.value.push(false);
					});

					this.datalist = res.body.message;


				});

			}
		}
	}
</script>

<style scoped>
	.row{
		border-bottom: 1px solid rgba(0,0,0,0.3);
		height: 102px;
		display: flex;
		padding: 5px;
	}

	.switch{
		flex:0 0 52px;
	}

	.img{
		margin-left: 5px;
		height: 75px;
		width: 75px;
		flex: 0 0 85px;
	}

	.inforight{
		margin-left: 5px;
		flex:1;
	}
	.inforight ul{
		padding-left: 0px;
	}
	.inforight li{
		list-style: none;
		display: inline-block;
	}

	.inforight h4{
		color: #0094ff;
		font-size: 14px;
	}

	.bottom li:first-child{
		color:red;
		margin-right: 5px;
	}

	.bottom li:last-child{
		margin-left: 5px;
	}
	#total{
		height: 100px;
		background-color: rgba(0,0,0,0.1);
		display: flex;
		padding: 5px;;
	}

	#total ul {
	 padding-left: 0px;
	}
	#total li{
		list-style: none;
		font-size: 14px;
	}

	#button{
		flex:0 0 60px;
		margin: 30px 0 0 0 ;
	}

	.desc{

		flex:1;
	}
</style>



