<template>
	<div id="tmpl">
	<!---->
	<!--1.0 -->
		<div id="postcomment">
			<h3></h3>
			<p class="p"></p>
			<textarea placeholder="..." v-model="postcontent"></textarea>
			<mt-button type="primary" size="large" @click="postcomment"></mt-button>
		</div>

		<!--2.0 -->
		<div id="list">
			<h3></h3>
			<p class="p"></p>
			<div v-for="(item,index) in list">
				<div class="title">
					<span>{{index + 1}}:</span>
					<span>{{item.user_name}}</span>
					<span>{{item.add_time | datefmt('YYYY-MM-DD HH:mm:ss')}}</span>
				</div>
				<ul class="mui-table-view">
					<li class="mui-table-view-cell" v-text="item.content"></li>
				</ul>
			</div>
		</div>

		<!--3.0 -->
		<mt-button type="danger" size="large" plain @click="getmore"></mt-button>
	</div>
</template>

<script>
	import common from '../../kits/common.js';
	import { Toast } from 'mint-ui';

	export default{
		data(){
			return {
				pageindex:1 , //api
				postcontent : '',  //
				list:[]  //
			}
		},
		props:['id'], //id
		created(){
//			1.0 
			this.getcommentlist(this.pageindex);
		},
		methods:{
//			3.0 
			getmore(){
//				1.0 this.pageindex1
				this.pageindex++;
				console.log(this.pageindex);

//				2.0 this.pageindex
				this.getcommentlist(this.pageindex);

			},
//			2.0  pageindex:1
			getcommentlist(pageindex){
				pageindex = pageindex || 1;
//				1.0 url
				let url  = common.apidomain + '/api/getcomments/'+this.id+'?pageindex='+pageindex;
//				2.0 ajax
				this.$http.get(url).then(function(res){
					if(res.body.status != 0 ){
						Toast(res.body.message);
						return;
					}
//3.0 				messagethis.list
					this.list = this.list.concat(res.body.message);
				});
			},
//			1.0 
			postcomment(){
//				0.0 
				if(this.postcontent.trim().length <= 0){
					Toast('');
					return;
				}
//				1.0  /api/postcomment/:artid
				let url = common.apidomain + '/api/postcomment/'+this.id;
//				2.0 ajaxpost
//				2.0.1  content:  
				this.$http.post(url,{content:this.postcontent},{emulateJSON:true}).then(function(res){
					Toast(res.body.message);

//					3.0 
				this.list = [{
						"user_name": "",
						"add_time": new Date(),
						"content": this.postcontent
					}].concat(this.list);

					//	4.0 
					this.postcontent = '';
				});
			}
		}
	}

</script>

<style scoped>
/* 1.0  */
#postcomment{
	padding: 5px;
}
	 .p{
		height: 1px;
		width: 100%;
		border-bottom: 1px solid rgba(0,0,0,0.3);
	}

	/*2.0 */
#list{
	padding: 5px;
}
.title{
	padding: 5px;
	color: #6d6d72;
	font-size: 15px;
	background-color: rgba(0,0,0,0.1);
}
</style>


