<template>
	<div id="tmpl">
	    <!--1.0 -->
		<div id="desc">
			<!--- -->
			<div class="title">
				<h4>{{photoinfo.title}}</h4>
				<p>
					{{photoinfo.add_time | datefmt('YYYY-MM-DD HH:mm:ss')}}  {{photoinfo.click}}
				</p>
				<p class="line"></p>
			</div>

			<!---->
			<div class="mui-content">
				<ul class="mui-table-view mui-grid-view mui-grid-9">
					<li  v-for="(item, index) in list" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
						<img class="preview-img"  :src="item.src" height="100" @click="$preview.open(index, list)"
								 >
					</li>
				</ul>
			</div>

			<!--- -->
			<p v-html="photoinfo.content"></p>
		</div>

		<!--2.03 -->
		<div id="comment">
			<comment :id="id"></comment>
		</div>
	</div>
</template>

<script>
	// 2.0.0 
	import comment from '../subcom/comment.vue';
	import common from '../../kits/common.js';
	import { Toast } from 'mint-ui';
	export default{
		components:{
			comment //2.0.1 
		},
		data(){
			return {
				id:0,  //id
				photoinfo :{
//					"id": 52,
//					"title": "Show",
//					"click": 0,
//					"add_time": "2015-04-18T06:27:06.000Z",
//					"content": ",Show,,,,,,,,"
				},  //
				list :[
//					{
//						src: 'https://placekitten.com/600/400',
//						w: 600,
//						h: 400
//					}, {
//						src: 'https://placekitten.com/1200/900',
//						w: 1200,
//						h: 900
//					}
				]  // 
			}
		},
		created(){
			this.id = this.$route.params.id;

//			ajax
			this.getinfo();

//			
			this.getimgs();
		},
		methods:{
//			2.0 
			getimgs(){
				let url = common.apidomain + '/api/getthumimages/'+this.id;
				this.$http.get(url).then(function(res){
					let body = res.body;
					if(body.status != 0 ){
						Toast(body.message);
						return;
					}

//					this.list
//					vue-preview {src:,w:,h:} w ,h 
					body.message.forEach(function(item){
//						400
						let img = document.createElement('img');
						img.src = item.src;
						item.h = img.height;
						item.w = img.width;
					});
					this.list = body.message;
				});

			},
//			1.0  
			getinfo(){
				let url =common.apidomain +'/api/getimageInfo/'+this.id;

				this.$http.get(url).then(function(res){
					let body = res.body;
					if(body.status != 0 ){
						Toast(body.message);
						return;
					}

//					this.photoinfo
					this.photoinfo = body.message[0];
				});
			}
		}

	}

</script>

<style scoped>
  /**/
  #desc{
	  padding: 10px;
  }
	#desc  .title h4{
		color: #0094ff;
	}

  #desc .title p{
	color:rgba(0,0,0,0.4);
	  margin-top: 10px;
  }

  #desc .title .line{
	  width: 100%;
	  height: 1px;
	  border-bottom: 1px solid rgba(0,0,0,0.4);
  }

  .mui-content,.mui-content ul{
	  background-color: #fff;
  }
  .mui-grid-view.mui-grid-9 .mui-table-view-cell{
	  border-right:0px;
	  border-bottom:0px;
  }
  .mui-grid-view.mui-grid-9{
	  border-top:0px;
	  border-left:0px;
  }

  /* 9*/
	.mui-content img{
		width: 100px;
		height: 100px;
	}
</style>


